<template>
  <div class="">
    <!-- <h1>Bereken hier uw krediet:</h1> -->
    <form class="" action="">
      <div class="selectArea">
          <label class="loan__title" for="lening">Wat is het doel van jouw lening?</label>
           <v-select v-model='selected' :options="options" ></v-select>
          <!-- <select v-model='selected' class="selectMenu">
            <option value="gezinsuitgaven" selected>Gezinsuitgaven</option>
            <option value="auto">Auto</option>
            <option value="renovatie">Renovatie</option>
            <option value="vakantie">Vakantie</option>
            <option value="huwelijk">Huwelijk</option>
            <option value="hergroeperen">Hergroeperen kredieten</option>
          </select> -->
      </div>
      <div class="bs-form__question__div">
        <h2 class="loan__title">Voer het bedrag in dat u wilt lenen</h2>
        <div class="rij bs-form__input__div">
          <button v-on:click.prevent="reduceTotalAmount();">-</button>
          <div>
            <label for="">EUR</label>
            <input v-model.number="bedrag" v-on:keydown.enter.prevent v-on:blur="check" v-on:click="selectAllNumbers" type="number">
          </div>
          <button v-on:click.prevent="increaseTotalAmount();">+</button>
        </div>
      </div>
      <div class="bs-form__question__div">
        <h2 class="loan__title" for="">Voor een periode van</h2>
        <div class="rij bs-form__input__div">
          <button v-on:click.prevent="reduceYears();">-</button>
          <div>
            <label for="">MAANDEN</label>
            <input v-model.number="jaar" v-on:keydown.enter.prevent v-on:blur="check" v-on:click="selectAllNumbers" type="number">
          </div>
          <button v-on:click.prevent="increaseYears();">+</button>
        </div>
      </div>
      <div v-if="error" class="error_msg">Er is geen simulatie mogelijk voor dit kredietbedrag en aantal maanden. Probeer een ander kredietbedrag / periode.</div>
      <!-- <div v-if="error" class="error_msg">Aucune simulation n'est possible pour ce montant de crédit et ce nombre de mois. Essayez un montant / période de crédit différent.</div> -->
      <div class="bereken-container">
        <button :disabled="error" v-bind:class="{buttonError : error}" v-if="mobile === true" v-scroll-to="'#element'" id="element" class="bs-form__bereken" type="submit" v-on:click.prevent="calculate(); showCalculation();">Berekenen jouw lening</button>
        <button :disabled="error" v-bind:class="{buttonError : error}" v-else class="bs-form__bereken" type="submit" v-on:click.prevent="calculate(); showCalculation()">Berekenen jouw lening</button>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  name: 'BerekeningSimulatieMaand',
  props: {
    
  },
  data() {
    return {
      mobile: false,
      options: [
      'gezinsuitgaven',
      'auto',
      'renovatie',
      'vakantie',
      'huwelijk',
      'hergroeperen'
    ]
    }
  },
  computed: {
    error(){
      return this.$store.state.error
    },
    berekendBedragPerMaand () {
      return this.$store.state.berekendBedragPerMaand
    },
    selected:{
      get () {
        return this.$store.state.typeLening
      },
      set (value) {
        this.$store.commit('updateLening', value)
        this.$store.commit('check')
      }
    },
    bedrag: {
      get () {
        return this.$store.state.totaalBedrag
      },
      set (value) {
        this.$store.commit('updateTotaalBedrag', value)
      }
    },
    jaar: {
      get () {
        return this.$store.state.jaarMonth
      },
      set (value) {
        this.$store.commit('updateYear', value)
      }
    }
  },
  mounted() {
    
    let vm = this;

    const changeButton = mediaQueryMobile => {
      if (mediaQueryMobile.matches) {
        vm.mobile = true
      } else {
        vm.mobile = false
      }
    }

    let mediaQueryMobile = window.matchMedia("(max-width: 700px)")
    changeButton(mediaQueryMobile)
    mediaQueryMobile.addListener(changeButton)


    this.createSelect() // Calls the method before page loads

  },
  methods: {
    createSelect() {

      var x, i, j, selElmnt, a, b, c;
/*look for any elements with the class "custom-select":*/
x = document.getElementsByClassName("custom-select");
for (i = 0; i < x.length; i++) {
  selElmnt = x[i].getElementsByTagName("select")[0];
  /*for each element, create a new DIV that will act as the selected item:*/
  a = document.createElement("DIV");
  a.setAttribute("class", "select-selected");
  a.innerHTML = selElmnt.options[selElmnt.selectedIndex].innerHTML;
  x[i].appendChild(a);
  /*for each element, create a new DIV that will contain the option list:*/
  b = document.createElement("DIV");
  b.setAttribute("class", "select-items select-hide");
  for (j = 1; j < selElmnt.length; j++) {
    /*for each option in the original select element,
    create a new DIV that will act as an option item:*/
    c = document.createElement("DIV");
    c.innerHTML = selElmnt.options[j].innerHTML;
    c.addEventListener("click", function(e) {
        /*when an item is clicked, update the original select box,
        and the selected item:*/
        var y, i, k, s, h;
        s = this.parentNode.parentNode.getElementsByTagName("select")[0];
        h = this.parentNode.previousSibling;
        for (i = 0; i < s.length; i++) {
          if (s.options[i].innerHTML == this.innerHTML) {
            s.selectedIndex = i;
            h.innerHTML = this.innerHTML;
            y = this.parentNode.getElementsByClassName("same-as-selected");
            for (k = 0; k < y.length; k++) {
              y[k].removeAttribute("class");
            }
            this.setAttribute("class", "same-as-selected");
            break;
          }
        }
        h.click();
    });
    b.appendChild(c);
  }
  x[i].appendChild(b);
  a.addEventListener("click", function(e) {
      /*when the select box is clicked, close any other select boxes,
      and open/close the current select box:*/
      e.stopPropagation();
      closeAllSelect(this);
      this.nextSibling.classList.toggle("select-hide");
      this.classList.toggle("select-arrow-active");
    });
}

    },
    showCalculation(){
      this.$store.commit('berekeningIsDone')
    },
    check(){
      this.calculate()
    },
    reduceTotalAmount() {
      this.$store.commit('reduceTotalAmount')
      this.calculate() 
    },
    increaseTotalAmount() {
      this.$store.commit('increaseTotalAmount')
      this.calculate() 
    },
    reduceYears() {
      this.$store.commit('reduceYears')
      this.calculate() 
    },
    increaseYears() {
      this.$store.commit('increaseYears')
      this.calculate() 
    },
    calculate() {
      this.$store.commit('check')
      this.$store.commit('calculateRente')
      this.$store.commit('calculatePerMonth')
      // this.$store.commit('berekeningIsDone')
      this.$store.commit('calculateInterestMaand')
    },
    selectAllNumbers(e) {
      e.target.select()
      e.target.focus()
    },

  }
}
</script>